@import url(https://fonts.googleapis.com/css?family=Muli);
.container {
  background-color: "#F7F8FC";
  padding: 30;
  height: 100vh;
  min-width: 850px;
}

.main-block {
  padding-top: 20px;
  padding-bottom: 20px;
  align-items: center;
  margin: auto;
  flex: 1 0 auto;
}

.block-list {
  align-items: flex-end;
}

.block {
  width: 10px;
  height: 50px;
  margin: 2px;
  display: inline-block;
}

.block-pending {
  background-color: rgb(25, 125, 255);
}

.block-processed {
  background-color: lightcoral;
}

.block-result {
  background-color: lightseagreen;
}

.block-curr {
  background-color: gold;
}

.block-target {
  margin-right: 100px;
  background-color: lightseagreen;
}

html,
body {
  height: 100%;
  font-family: sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

#root {
  display: flex;
  flex-direction: column;
  height: 100%;
}

